h3 {
    font-size: 2.8rem;
    text-align: center;
    text-transform: uppercase;
}
ul {
    padding: 0;
    list-style: none;
}

.seccion{
    padding: 3rem 0;
    @include m.tablet { 
        padding: 8rem 0;
    }
}